.footer{
  .main--background{
    position: relative;
    background: url('../images/footer-background.jpg') no-repeat;
    background-size:100%;
    .footer__container{
      .footer__list{
        .footer__wrapper{
          z-index: 1;
          .footer__sidebar-left{
            .footer-left-sidebar{
              .title--section{
                color:#fff;
                font-weight: 400;
                font-size:18px;
              }
              .textwidget{
                .nw__post-item{
                  .nw__infomation{
                    .nw__post-title{
                      a{
                        color:#fff;
                      }
                      a:hover{
                      	color:var(--primary-color);
                      }
                    }
                    .nw__editor-date{
                      span{
                        color:#aaa;
                      }
                    }
                  }
                }
              }
            }
          }
          .footer__sidebar-center{
            .footer-mid-sidebar{
              .title--section{
                color:#fff;
                font-weight: 400;
                font-size:18px;
              }
              .textwidget{
                .nw__post-item{
                  .nw__infomation{
                    .nw__post-title{
                      a{
                        color:#fff;
                      }
                      a:hover{
                      	color:var(--primary-color);
                      }
                    }
                    .nw__editor-date{
                      span{
                        color:#aaa;
                      }
                    }
                  }
                }
              }
            }
          }
          .footer__sidebar-right{
            .footer-right-sidebar{
              .title--section{
                color:#fff;
                font-weight: 400;
                font-size:18px;
              }
              .textwidget{
                .category-item{
                  span{
                    color:#fff;
                    font-weight: 400;
                    a{
                      color:#fff;
                      font-weight: 400;
                    }
                    a:hover{
                    	color:var(--primary-color);
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .divine-footer{
      padding: 0.5px;
      background: #fff;
      margin: 25px auto;
    }
    .footer__sub{
      .footer__sub-background{
        .logo-footer{
          align-self: center;
          z-index: 1;
          a{
            text-align: center;
            img{
              width:150px;
            }
          }
        }
        .about-us{
          z-index: 1;
          .footer-sub-mid-sidebar{
            .title--section{
              font-size:18px;
              color:#fff;
            }
            .textwidget{
              color:#fff;
            }
          }
        }
        .follow-us{
          z-index: 1;
          .footer-sub-right-sidebar{
            .title--section{
              font-size:18px;
              color:#fff;
            }
            .custom-html-widget{
              font-size:24px;
              a.mc-social {
                margin-right: 10px;
                color:#fff;
              }
            }
          }
        }
      }
    }
  }
  .bg-color-cover{
    background: #000;
    opacity: .5;
    width: 100%;
    height: 100%;
    top: 0;
    z-index: 0;
    position: absolute;
  }
}
